import React from 'react';
import PostLists from './PostLists';
import PostForm from './PostForm';

import classes from './Posts.module.scss';

function CreatePosts() {
    const [posts, setPosts] = React.useState([
        { id: 1, url: 'https://images.unsplash.com/photo-1633311905139-7b6088a69e33?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1332&q=80', title: '–ò—Å–∫—É—Å—Å—Ç–≤–µ–Ω–Ω—ã–π –∏–Ω—Ç–µ–ª–ª–µ–∫—Ç', body: '–í —ç—Ç–æ—Ç —Å–µ–∫—Ç–æ—Ä –≤—Ö–æ–¥—è—Ç —Ä–∞–∑—Ä–∞–±–æ—Ç–∫–∏ –≤ –æ–±–ª–∞—Å—Ç–∏ –º–∞—à–∏–Ω–Ω–æ–≥–æ –æ–±—É—á–µ–Ω–∏—è, –Ω–µ–π—Ä–æ—Å–µ—Ç–µ–π, —Ä–æ–±–æ—Ç–æ—Ç–µ—Ö–Ω–∏–∫–∏, —Ä–∞—Å–ø–æ–∑–Ω–∞–≤–∞–Ω–∏—è –ª–∏—Ü–∞ –∏ —Ä–µ—á–∏. –ë–ª–∞–≥–æ–¥–∞—Ä—è –Ω–µ–π—Ä–æ—Å–µ—Ç—è–º –±–æ—Ä—Ç–æ–≤–æ–π –∫–æ–º–ø—å—é—Ç–µ—Ä —Å–∞–º–æ—É–ø—Ä–∞–≤–ª—è–µ–º–æ–≥–æ —Ç—Ä–∞–Ω—Å–ø–æ—Ä—Ç–∞ –º–æ–∂–µ—Ç –æ—Ç–ª–∏—á–∏—Ç—å –ø–µ—à–µ—Ö–æ–¥–∞ –æ—Ç –ø—Ä–æ—á–∏—Ö –æ–±—ä–µ–∫—Ç–æ–≤ –Ω–∞ —Ç—Ä–∞—Å—Å–µ. –ò–Ω–≤–µ—Å—Ç–∏—Ä–æ–≤–∞—Ç—å –≤ –∏—Å–∫—É—Å—Å—Ç–≤–µ–Ω–Ω—ã–π –∏–Ω—Ç–µ–ª–ª–µ–∫—Ç –∏ —Ä–æ–±–æ—Ç–æ—Ç–µ—Ö–Ω–∏–∫—É –º–æ–∂–Ω–æ —á–µ—Ä–µ–∑ –Ω–µ—Å–∫–æ–ª—å–∫–æ —Ñ–æ–Ω–¥–æ–≤: IRBO, ROBO, BOTZ, ROBT. –ú–Ω–µ –ø–æ–Ω—Ä–∞–≤–∏–ª—Å—è –≥–ª–æ–±–∞–ª—å–Ω—ã–π —Ñ–æ–Ω–¥ IRBO ‚Äî iShares Robotics and Artificial Intelligence ETF. –û–Ω —Å–∞–º—ã–π —Å–≤–µ–∂–∏–π: –∑–∞–ø—É—â–µ–Ω –≤ –∞–≤–≥—É—Å—Ç–µ 2018 –≥–æ–¥–∞. –°—Ç–æ–∏–º–æ—Å—Ç—å –æ–±—Å–ª—É–∂–∏–≤–∞–Ω–∏—è –∑–Ω–∞—á–∏—Ç–µ–ª—å–Ω–æ –Ω–∏–∂–µ, —á–µ–º —É –∫–æ–Ω–∫—É—Ä–µ–Ω—Ç–æ–≤, ‚Äî 0,47%.' }
    ]);

    function createPost(newPost) {
        setPosts([...posts, newPost]);
    }

    function remove(post) {
        const isInList = !!posts.find((item) => item.id === post.id);
        const newList = isInList
            ? posts.filter((item) => item.id !== post.id)
            : [...posts, post];
        setPosts(newList);
    }

    const pageTitle = ['–°–æ–∑–¥–∞–π—Ç–µ —Å–≤–æ–π –ø–æ—Å—Ç –æ–± –∏–Ω–≤–µ—Å—Ç–∏—Ü–∏—è—Ö'];

    return (
        <div className="Posts">
            <h2 className={classes.pageTitle}>{pageTitle}</h2>
            <div className="container">
                <PostLists remove={remove} posts={posts} />
                <PostForm create={createPost} />
            </div>
        </div>
    )
}

export default CreatePosts;