import React from 'react';

import classes from './Posts.module.scss';

function PostForm({ create }) {

    const [post, setPost] = React.useState({ title: '', url: '', body: '' })

    function addPost(e) {
        e.preventDefault();
        const newPost = { ...post, id: Date.now() };
        setPost({ title: '', url: '', body: '' });
        create(newPost);
    }

    return (
        <div className={classes.inputs}>
            <input className={classes.input} value={post.title} onChange={e => setPost({ ...post, title: e.target.value })} type="text" placeholder="–ó–∞–≥–æ–ª–æ–≤–æ–∫" />
            <input className={classes.input} value={post.url} onChange={e => setPost({ ...post, url: e.target.value })} type="url" placeholder="–°—Å—ã–ª–∫–∞ –Ω–∞ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ" />
            <input className={classes.input} value={post.body} onChange={e => setPost({ ...post, body: e.target.value })} type="text" placeholder="–û–ø–∏—Å–∞–Ω–∏–µ –ø–æ—Å—Ç–∞" />
            <button onClick={addPost} type="submit">–°–û–ó–î–ê–¢–¨</button>
        </div>
    )
}

export default PostForm;